// 
const events = {
    redirect(url) {
        if (url) {
            window.location.href = url
        }
    },

    alert(msg) {
        if (msg) {
            alert(msg)
        }
    },
}

const mixins = {
    methods: events,
}

const eventList = [
    {
        key: 'redirect',
        label: '',
        event: events.redirect,
        param: '',
    },
    {
        key: 'alert',
        label: 'alert ',
        event: events.alert,
        param: '',
    },
]

export {
    mixins,
    events,
    eventList,
}